/* ================================================================ */
/*  $1 - Resets
/* ================================================================ */


* {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  box-sizing: border-box;
}

html {    
  color: var(--color-neutral-9);
  font-family: Hilda, Roboto, Helvetica, Arial, sans-serif;
  overflow: auto;
  overflow-x: hidden;
  -moz-osx-font-smoothing: grayscale;
  -ms-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%;
  -servicestudio-overflow: initial;
  
}

body {    
  background-color: var(--color-neutral-2); 
  background-color: #181818 !important; 
  font-family: 'Hilda';
  font-size: var(--font-size-base);
  color: var(--color-body);
}

.ie11,
.edge {
scrollbar-arrow-color: var(--color-neutral-2);
scrollbar-face-color: var(--color-neutral-2);
scrollbar-base-color: var(--color-neutral-0);
scrollbar-3dlight-color: var(--color-neutral-0);
scrollbar-highlight-color: var(--color-neutral-0);
scrollbar-track-color: var(--color-neutral-0);
scrollbar-shadow-color: var(--color-neutral-0);
scrollbar-dark-shadow-color: var(--color-neutral-0);
}

/* ================================================================ */
/*  $2 - HTML Element
/* ================================================================ */

/* Inputs placeholder color */

::-webkit-input-placeholder     { color: var(--color-neutral-7); }
::-moz-placeholder              { color: var(--color-neutral-7); }
:-ms-input-placeholder          { color: var(--color-neutral-7); }
:-moz-placeholder               { color: var(--color-neutral-7); }



/* AUTOCOMPLETE STYLES */

input:-internal-autofill-selected,
.input:-internal-autofill-selected {
background-color: var(--color-neutral-0);
box-shadow: inset 100px 100px var(--color-neutral-0);
}

.input:-webkit-autofill,
.input:-webkit-autofill:hover, 
.input:-webkit-autofill:focus,
.textarea:-webkit-autofill,
.textarea:-webkit-autofill:hover,
.textarea:-webkit-autofill:focus,
.select:-webkit-autofill,
.select:-webkit-autofill:hover,
.select:-webkit-autofill:focus {
font-family: 'Hilda';
background-color: var(--color-neutral-0);
-webkit-text-fill-color: var(--color-neutral-10);
box-shadow: inset 100px 100px var(--color-neutral-0);
}


.input, 
textarea.input, 
.select,
.layout .Form .input.ReadOnly:not(.Not_Valid), 
.layout .Form .select.ReadOnly:not(.Not_Valid) {
  /* -webkit-text-fill-color: var(--color-neutral-9); */
  background-color:  var(--color-neutral-0); 
  border: var(--border-size-s) solid var(--color-neutral-4);
  border-radius: var(--border-radius-none);
  font-weight: var(--font-light);
  padding: var(--space-none) var(--space-s);
}

.input, 
textarea.input, 
.select {   
  color: var(--color-neutral-9);
  transition: all 180ms linear;
  font-family: inherit;
  -webkit-appearance: none;
  line-height: 1.2;
}

textarea.input {
  padding: var(--space-s);
}

.input, 
.select {
  height: 32px;
}

input, 
textarea, 
select {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  font-family: inherit;
  font-size: 100%;
  line-height: 1;
  margin: 0;
  -webkit-text-fill-color: var(--color-neutral-9);
}

/* SELECT ORIGINAL */
.select {
  background-image: url('data:image/png;base64,');
  background-position: center right 10px;
  background-repeat: no-repeat;
  background-size: 8px 4px;
  border: var(--border-size-s) solid var(--color-neutral-4);
  border-radius: var(--border-radius-none);
  -webkit-text-fill-color: var(--color-neutral-9);
}

.input:hover, 
.textarea:hover, 
.select:hover {
  border-color: var(--color-neutral-5);
}

[disabled="disabled"],
[readonly="readonly"],
textarea[readonly="readonly"] {
  background-color: var(--color-neutral-6);
  border: 1px solid var(--color-neutral-4);
  color: var(--color-neutral-6);
  -webkit-text-fill-color: var(--color-neutral-9);
}

.input-small {
  height: 22px;
}

.input.changed:not(.Not_Valid)  {
  border-left-color: var(--color-primary);
  border-top-color: var(--color-border-input);
  border-right-color: var(--color-border-input);
  border-bottom-color: var(--color-border-input);
  border-left-width: var(--border-size-m);
  -webkit-text-fill-color: var(--color-neutral-9);
}

.input:focus,
.input.changed:focus {
  border: var(--border-size-s) solid var(--color-primary);
}

.input:hover,
.input.changed:hover{
  border-color: var(--color-border-input-hover);
  border-width: var(--border-size-s);
}

.input.Not_Valid, 
.textarea.Not_Valid, 
.select.Not_Valid {
  border-left-color: var(--color-error);
  border-top-color: var(--color-border-input);
  border-right-color: var(--color-border-input);
  border-bottom-color: var(--color-border-input);
  border-left-width: var(--border-size-m);
}

/* Input Arrows */
input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button {   
  color: var(--color-neutral-9);
  background-color: var(--color-neutral-2); 
}

/* NO INPUT ARROWS - USE THE STYLE BELLOW IF YOU DON'T WANNA THE ARROWS ON INPT NUMBERS */

/*
input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  margin: 0; 
}
*/

.ValidationMessage {
  color: var(--color-body);
  font-weight: var(--font-light);
  opacity: 1;
}

a:not(.btn, .btn-drop-option), 
a:not(.btn, .btn-drop-option):visited {
  color: var(--color-links);
  border-bottom: none;
}

a:not(.btn):hover, a:not(.dropdown-menu):hover,
a:not(.btn):visited:hover, a:not(.dropdown-menu):visited:hover,
a:not(.btn).link-hover, a:not(.dropdown-menu).link-hover,
a:not(.btn):visited.link-hover, a:not(.dropdown-menu):visited.link-hover {
