}

/* Workaround for ie8 */
/* IE10 or less */
body .EditableTable > tbody > tr{
  display: none\9;
}

/* Workaround for ie8 */
/* IE 9 or over */
:root body .EditableTable > tbody > tr{
  display: table-row;
}

body .EditableTable > tbody > tr:last-child {
  display: none;
}

.EditableTable .ColumnHeader {
