/*Icon cross animation*/
#openCloseIcon{
  transition:0.3s !important;
}
#openCloseIcon.lines-icon {
  position: fixed;
  float:right;
  overflow: hidden;
  margin: 0;
  padding: 0;
  width: 16px;
  height: 16px;
  font-size: 0;
  text-indent: -9999px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  box-shadow: none;
  border-radius: none;
  border: none;
  cursor: pointer;
  -webkit-transition: background 0.3s;
  transition: background 0.3s;
}
#openCloseIcon.lines-icon:focus {
  outline: none;
}
#openCloseIcon.lines-icon span {
  display: block;
  position: absolute;
  left: 0px;
  right: 0px;
  height: 2px;
  background: transparent;
  border-radius: 0.57143rem;
}
#openCloseIcon.lines-icon span::before, #openCloseIcon.lines-icon span::after {
  position: absolute;
  display: block;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: var(--color-primary);;
  border-radius: 0.57143rem;
  content:"";
}
#openCloseIcon.lines-icon span::before {
  top: 7px;
}
#openCloseIcon.lines-icon span::after {
  bottom: -14px;
}
.lines {
  background: none;
}
.lines span {
  -webkit-transition: background 0s 0.3s;
  transition: background 0s 0.3s;
}
.lines span::before, .lines span::after {
  -webkit-transition-duration: 0.3s, 0.3s;
  transition-duration: 0.3s, 0.3s;
  -webkit-transition-delay: 0.3s, 0s;
  transition-delay: 0.3s, 0s;
}
.lines span::before {
  -webkit-transition-property: top, -webkit-transform;
  transition-property: top, transform;
}
.lines span::after {
  -webkit-transition-property: bottom, -webkit-transform;
  transition-property: bottom, transform;
}
.lines.close {
  background: none;
}
.lines.close span {
  background: none;
}
.lines.close span::before {
  top: 9px !important;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}
.lines.close span::after {
  bottom: -9px !important;
  -webkit-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  transform: rotate(-45deg);
}
.lines.close span::before, .lines.close span::after {
  -webkit-transition-delay: 0s, 0.3s;
  transition-delay: 0s, 0.3s;
}